  background-color: #78bcfc;
  background-image: image-set("../img/feedback.webp" 1x, "../img/feedback.jpg" 1x);
  background-position: top -140px left;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .feedback {
    background-image: image-set("../img/feedback@2x.webp" 2x, "../img/feedback@2x.jpg" 2x);
  }
}
@media (max-width: 1365px) {
  .feedback {
    background-size: 1366px;
  }
}
.feedback .is-invalid {
  box-sizing: border-box;
  outline: 2px solid #ff1553;
}
.feedback button {
  padding: 10px 15px 12px;
  margin-top: 29px;
  width: 100%;
  justify-content: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
}

.feedback__wrapper {
  margin: 0 auto;
  max-width: 1920px;
  padding: 50px clamp(30px, 11.9326500732%, 220px) 50px clamp(30px, 11.9326500732%, 220px);
  display: grid;
  grid-template-columns: 1fr 38.75%;
  gap: 5%;
}

.feedback__info {
  padding: 163px 0 163px 0;
}
.feedback__info h3 {
  padding: 0;
  margin: 0 0 8px 0;
  font-weight: 700;
  font-size: 40px;
  line-height: 150%;
}
.feedback__info p {
  padding: 0;
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
}

.feedback__form {
  display: flex;
  flex-direction: column;
  background-color: #011c40;
  padding: 44px 50px 50px 50px;
  color: #f9fbfd;
}
.feedback__form p {
  padding: 0;
  margin: 0 0 27px 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
}
.feedback__form input {
  padding: 12px 16px;
  box-sizing: border-box;
  border: none;
  background: #f9fbfd;
  position: relative;
}
.feedback__form input::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: rgba(1, 28, 64, 0.7);
}

input[type=text],
input[type=tel],
input[type=email] {
  width: 100%;
  position: relative;
}
input[type=text]:invalid,
input[type=tel]:invalid,
input[type=email]:invalid {
  outline-color: #ff1553;
}

.is-invalid:focus-visible + span {
  display: block;
}

.feedback__input-name,
.feedback__input-phone,
.feedback__input-email {
  position: relative;
}
.feedback__input-name span,
.feedback__input-phone span,
.feedback__input-email span {
  display: none;
  position: absolute;
  top: 55px;
  left: -14px;
  padding: 5px;
  color: #ff1553;
  z-index: 999;
  font-size: 15px;
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 5px 5px 5px #000000;
}
.feedback__input-name span::before,
.feedback__input-phone span::before,
.feedback__input-email span::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 10px;
  background-color: #ffffff;
  top: -10px;
  left: 11px;
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

.feedback__input-name {
  margin: 0 0 20px 0;
}

.feedback__input-phone {
  margin: 0 0 21px 0;
}

.feedback__input-email {
  margin: 0 0 16px 0;
}

input[type=checkbox] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
input[type=checkbox]:checked + span {
  background-image: url("../img/svg/checkbox.svg");
  background-repeat: no-repeat;
  background-size: 12px;
  background-position: 50%;
  color: #ffffff;
}
input[type=checkbox]:focus + span {
  outline: 2px solid #b77900;
}
input[type=checkbox]:invalid + span {
  outline-color: #ff1553;
}
input[type=checkbox]:invalid + span::after {
  color: #ff1553;
  content: "Необходимо согласиться на обработку данных";
  display: block;
  width: 310px;
  position: absolute;
  font-size: 15px;
  top: 30px;
  left: 0;
  z-index: 999;
}

.feedback__checkbox {
  margin-left: 23px;
}
.feedback__checkbox label {
  font-weight: 400;
  font-size: 10px;
  line-height: 130%;
  position: relative;
}
.feedback__checkbox span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -23px;
  box-sizing: border-box;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}

.contacts__wrapper {
  max-width: 1920px;
  margin: 0 auto;
  padding: 118px clamp(30px, 7.5402635432%, 110px) 122px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
}

.contacts__map {
  display: flex;
  align-items: center;
  min-height: 306px;
}
.contacts__map img {
  object-fit: contain;
}
.contacts__map iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.contacts__text {
  padding: 77px 0 10px 60px;
}
.contacts__text p {
  padding: 0;
  margin: 0 0 32px 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
}
.contacts__text span {
  display: block;
  padding-left: 28px;
  margin-bottom: 22px;
}
.contacts__text a {
  display: block;
  padding-left: 28px;
}
@media (hover: hover) {
  .contacts__text a:hover:not(.focus-visible) {
    color: #78bcfc;
  }
  .contacts__text a:hover:not(.focus-visible) + svg {
    color: #78bcfc;
  }
}
.contacts__text svg {
  position: absolute;
  top: 0;
  left: 0;
}

.contacts__block {
  position: relative;
}

.footer {
  background-color: #011c40;
}

.footer__wrapper {
  max-width: 1920px;
  margin: 0 auto;
  padding: 50px clamp(30px, 7.5402635432%, 110px);
  display: flex;
  justify-content: space-between;
}

.footer__logo {
  min-width: 70px;
  margin-right: 22px;
}
.footer__logo a {
  display: block;
  color: #f9fbfd;
}
@media (hover: hover) {
  .footer__logo a:hover:not(.focus-visible) {
    color: #78bcfc;
  }
}
.footer__logo svg {
  display: block;
}

.footer__nav {
  display: flex;
  flex: auto;
}

.footer__list {
  display: flex;
  flex-wrap: wrap;
}
.footer__list--social {
  margin: 0 -17px 0 0;
}

.footer__list-item {
  display: flex;
  align-content: center;
}
.footer__list-item a {
  padding: 9px 14px;
  margin: 0 3px;
  font-weight: 500;
  font-size: 16px;
  line-height: 125%;
  color: #f9fbfd;
}
@media (hover: hover) {
  .footer__list-item a:hover:not(.focus-visible) {
    color: #78bcfc;
  }
}
.footer__list-item--social a {
  padding: 10px 9px 9px 9px;
  display: inline-flex;
  line-height: 0;
  margin: 0 9px 0 0;
}

