.footer {
  background-color: $color-tangaroa;
}

.footer__wrapper {
  max-width: 1920px;
  margin: 0 auto;
  padding: 50px clamp($wrapper-padding-desktop-min, $wrapper-padding-desktop, $wrapper-padding-desktop-max);
  display: flex;
  justify-content: space-between;
}

.footer__logo {
  min-width: 70px;
  margin-right: 22px;

  a {
    display: block;
    color: $color-alice-blue;

    @include hover {
      color: $color-maya-blue;
    }
  }

  svg {
    display: block;
  }
}

.footer__nav {
  display: flex;
  flex: auto;
}

.footer__list {
  display: flex;
  flex-wrap: wrap;

  &--social {
    margin: 0 -17px 0 0;
  }
}

.footer__list-item {
  display: flex;
  align-content: center;

  a {
    padding: 9px 14px;
    margin: 0 3px;
    font-weight: 500;
    font-size: 16px;
    line-height: 125%;
    color: $color-alice-blue;

    @include hover {
      color: $color-maya-blue;
    }
  }

  &--social {
    a {
      padding: 10px 9px 9px 9px;
      display: inline-flex;
      line-height: 0;
      margin: 0 9px 0 0;
    }
  }
}
